import React from "react";
import { Preloader } from "@faharmony/components";
import { Div100vh } from "@faharmony/views";
import {
  Theme,
  setTheme,
  getCurrentDarkModePreference,
} from "@faharmony/theme";

// Pre loading screen
export const Loader = () => {
  setTheme({ enableDarkMode: getCurrentDarkModePreference() });
  return (
    <Div100vh
      id="full-height-wrapper"
      style={{ width: "100vw", background: Theme.colorBackground("base") }}
    >
      <Preloader />
    </Div100vh>
  );
};

export default Loader;
