import React from "react";
import { styled, Theme, TintType } from "@faharmony/theme";
import { faTimes } from "@faharmony/icons";
import { OnClickEventType } from "@faharmony/helpers";
import { Text } from "../Text";
import { Icon, IIconProps } from "../Icon";
import { Box } from "../Box";

interface ITagProps {
  /** Text visible in Tag */
  value?: string;
  /** Text visible in Tag */
  children?: React.ReactNode;
  /** Color of Tag @default inverse */
  color?: TintType;
  /** Maximum width of Tag in pixels. @default 140 */
  maxWidth?: number;
  /** Function to execute when tag is closed. Shows a close icon. */
  onClose?: (e?: OnClickEventType) => void;
  /** Name of FontAwesome (light variant) icon in faCamelCase. */
  icon?: IIconProps["icon"];
}

const StyledTag = styled(Box)<ITagProps>`
  /* height: 20px; */
  display: inline-block;
  text-align: center;
  width: max-content;
  max-width: ${(p) => (p.maxWidth ? p.maxWidth : 140)}px;
  padding: 0px ${() => Theme.spacing("sm")};
  vertical-align: bottom;
  border-radius: ${() => Theme.radius("xs")};
  border: 1px solid
    ${(p) =>
      p.color
        ? Theme.colorTint(p.color, 50)
        : Theme.colorOutline("shadowHover")};
  background-color: ${(p) =>
    Theme.colorTint(p.color ? p.color : "inverse", 20)};
  color: ${(p) =>
    p.color ? Theme.colorTint(p.color) : Theme.colorText("secondary")};
  user-select: none;

  svg {
    align-self: center;
  }
  .closeIcon {
    cursor: pointer;
  }
`;

/**
 * Tag
 * ---
 * Used for purpose of displaying information or filters. The size matches h5 and p Text component.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 *
 * @example
 * <Tag color="danger">Deprecated</Tag>
 */
const Tag = React.forwardRef<HTMLDivElement, ITagProps>((props, ref) => (
  <StyledTag ref={ref} className="Tag tag" {...props} spacing="xs">
    {props.icon && <Icon icon={props.icon} size="sm" />}
    <Text truncate variant="small">
      {props.value || props.children || ""}
    </Text>
    {props.onClose && (
      <Icon
        icon={faTimes}
        size="sm"
        onClick={props.onClose}
        className="closeIcon"
      />
    )}
  </StyledTag>
));

export { Tag, ITagProps };
