import React from "react";
import { preventDefaultOnClick } from "@faharmony/helpers";

import {
  Popover,
  IPopoverProps,
  IPopoverConfirmProps,
  IPopoverMenuItemProps,
} from "../Popover";
import { Button, IButtonProps } from "../Button";

interface IButtonPopoverProps extends IButtonProps {
  /** Popover placement around trigger @default auto */
  popoverPlacement?: IPopoverProps["placement"];
  /** Open popover on left/right (click/contextmenu) mouse click.
   * On touch devices, click is replaced with press, and contextmenu is replaced by long-press.
   * @default click */
  popoverTrigger?: "click" | "contextmenu" | "mouseover";
  /** Display a menu dropdown when button is clicked. Overrides onClick. */
  popoverMenuItems?: IPopoverMenuItemProps[];
  /** Display a confirmation popup on click. Overrides onClick. */
  popoverConfirmOptions?: IPopoverConfirmProps;
  /** Display a custom popover on click. The callback receives popoverInstance containing a method to close the popover. */
  popoverContentCallback?: IPopoverProps["content"];
}

interface IIconButtonPopoverObject
  extends Omit<
    IButtonPopoverProps,
    "value" | "children" | "fullWidth" | "loadingText"
  > {
  id: string;
}
interface IButtonPopoverObject extends IIconButtonPopoverObject {
  value?: IButtonProps["value"];
}

/**
 * ButtonPopover
 * ---
 * Description of component
 * @jira https://fasolutions.jira.com/browse/HAR-ticker
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <ButtonPopover />
 */
const ButtonPopover = React.forwardRef<HTMLButtonElement, IButtonPopoverProps>(
  (props, ref) => {
    const {
      popoverConfirmOptions,
      popoverContentCallback,
      popoverMenuItems,
      popoverPlacement = "auto",
      popoverTrigger = "click",
      ...buttonProps
    } = props;
    const button = (
      <Button
        {...buttonProps}
        ref={ref}
        onClick={
          popoverTrigger === "click"
            ? preventDefaultOnClick
            : buttonProps.onClick
        }
        onContextMenu={
          popoverTrigger === "contextmenu"
            ? preventDefaultOnClick
            : buttonProps.onContextMenu
        }
      />
    );
    const popoverCommonProps: Partial<IPopoverProps> = {
      placement: popoverMenuItems ? "bottom-start" : popoverPlacement,
      trigger: popoverTrigger,
    };
    if (popoverContentCallback)
      return (
        <Popover {...popoverCommonProps} content={popoverContentCallback}>
          {button}
        </Popover>
      );
    if (popoverMenuItems)
      return (
        <Popover.Menu {...popoverCommonProps} menuItems={popoverMenuItems}>
          {button}
        </Popover.Menu>
      );
    if (popoverConfirmOptions)
      return (
        <Popover.Confirm {...popoverCommonProps} {...popoverConfirmOptions}>
          {button}
        </Popover.Confirm>
      );
    return button;
  }
);

export {
  ButtonPopover,
  IButtonPopoverProps,
  IIconButtonPopoverObject,
  IButtonPopoverObject,
};
