import React from "react";
import { Button } from "../Button";
import { Text } from "../Text";
import {
  faAngleRight,
  faAngleLeft,
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@faharmony/icons";

import { ICalendarProps } from "./types";
import * as Style from "./style";
import {
  useCalendar,
  weekRange,
  MONTHS_SHORT,
  DAYS_OF_THE_WEEK_SHORT,
  isSameDate,
} from "./helper";
import { uuid } from "@faharmony/helpers";

/**
 * Calendar
 * ---
 * Standalone simple calendar component. Inspired from https://dev.to/zhiyueyi.
 * @jira https://fasolutions.jira.com/browse/HAR-74
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <Calendar onChange={(date) => { console.log(date) }}/>
 */
const Calendar = (props: ICalendarProps) => {
  const { state, actions } = useCalendar(props);
  const { disabledDates = [], showOptionsFooter = false } = props;
  const weekArray = Array(7)
    .fill(0)
    .map((_, i) => (i + 1 > 6 ? 0 : i + 1));

  const weeks = weekRange(state.firstWeekOfMonth, state.lastWeekOfMonth);

  return (
    <Style.StyledCalendar className="Calendar">
      <Style.Navigation>
        <Button
          onClick={actions.handlePrevYear}
          icon={faAngleDoubleLeft}
          tooltip="Previous year"
          spacing="xs"
        />
        <Button
          onClick={actions.handlePrevMonth}
          icon={faAngleLeft}
          tooltip="Previous month"
          spacing="xs"
        />
        <Text
          value={MONTHS_SHORT[state.currentMonth] + " " + state.currentYear}
          variant="h5"
          align="center"
        />
        <Button
          onClick={actions.handleNextMonth}
          icon={faAngleRight}
          tooltip="Next month"
          spacing="xs"
        />
        <Button
          onClick={actions.handleNextYear}
          icon={faAngleDoubleRight}
          tooltip="Next year"
          spacing="xs"
        />
      </Style.Navigation>

      <Style.Table>
        <thead>
          <tr>
            {weekArray.map((wd) => (
              <Style.Tile className="dayName" key={wd}>
                <Text
                  variant="small"
                  align="center"
                  value={DAYS_OF_THE_WEEK_SHORT[wd]}
                />
              </Style.Tile>
            ))}
          </tr>
        </thead>
        <tbody>
          {weeks.map((weekNumber) => {
            const currentWeek = state.allWeeksInMonth[weekNumber];
            if (currentWeek)
              return (
                <Style.Row key={weekNumber} weekNumber={weekNumber.toString()}>
                  {weekArray.map((weekday) => {
                    const _date = currentWeek[weekday];
                    const dayNum = _date ? _date.getDate() : -1;
                    const renderTile =
                      (weekNumber === state.firstWeekOfMonth && dayNum > 7) ||
                      (weekNumber === state.lastWeekOfMonth && dayNum < 20) ||
                      dayNum < 1
                        ? ""
                        : dayNum;
                    const disabled =
                      _date && disabledDates.some((d) => isSameDate(d, _date));
                    return _date && renderTile !== "" ? (
                      <Style.Tile
                        key={_date.toString()}
                        isToday={isSameDate(_date, new Date())}
                        isSelected={isSameDate(_date, state.selectedDate)}
                        isDisabled={disabled}
                        onClick={() =>
                          disabled ? {} : actions.handleSelectDate(_date)
                        }
                      >
                        {renderTile}
                      </Style.Tile>
                    ) : (
                      <Style.Tile key={uuid()} isDisabled={true} />
                    );
                  })}
                </Style.Row>
              );
            else return null;
          })}
        </tbody>
      </Style.Table>
      {showOptionsFooter && (
        <Style.Options>
          <Button
            value="Yesterday"
            onClick={() => actions.handlePrevDay(new Date())}
            spacing="xs"
            fullWidth
          />
          <Button
            value="Today"
            onClick={() => actions.handleSelectDate(new Date())}
            spacing="xs"
            fullWidth
          />
          <Button
            value="Tomorrow"
            onClick={() => actions.handleNextDay(new Date())}
            spacing="xs"
            fullWidth
          />
        </Style.Options>
      )}
    </Style.StyledCalendar>
  );
};

export { Calendar, ICalendarProps };
