/** @see https://github.com/dvtng/react-loading-skeleton */
import { Theme, styled, keyframes, CSSProperties } from "@faharmony/theme";

interface ISkeletonProps {
  /** Height of the skeleton @default '20px' */
  height?: CSSProperties["height"];
  /** Width of the Skeleton @default '100%' of parent width */
  width?: CSSProperties["width"];
  /** Duration of single animation cycle (in seconds) @default 1.2 */
  duration?: number;
  /** Renders a circular shape instead of rectangular */
  circle?: boolean;
}

const defaultBaseColor = Theme.props.colorBackground.disabled;
const defaultHighlightColor = Theme.props.colorBackground.base;
const skeletonKeyframes = keyframes`
  0% {
    background-position: -200px 0;
  }
  100% {
    background-position: calc(200px + 100%) 0;
  }
`;

/**
 * Skeleton
 * ---
 * Display a placeholder preview of your content before
 * the data gets loaded to reduce load-time frustration.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.6.0
 *
 * @example
 * <Skeleton width="200px" />
 */
const Skeleton = styled.div<ISkeletonProps>`
  height: ${(p) => p.height || "20px"};
  width: ${(p) => p.width || "100%"};
  background-color: ${defaultBaseColor};
  background-image: linear-gradient(
    90deg,
    ${defaultBaseColor},
    ${defaultHighlightColor},
    ${defaultBaseColor}
  );
  background-size: 200px 100%;
  background-repeat: no-repeat;
  border-radius: ${(p) => (p.circle ? "50%" : Theme.props.radius.sm)};
  display: inline-block;
  line-height: 1;
  animation: ${skeletonKeyframes} ${(p) => p.duration || 1.2}s ease-in-out
    infinite;
`;

export { Skeleton, ISkeletonProps };
