import React, { useMemo } from "react";
import { styled } from "@faharmony/theme";
import { motion, useSpring } from "@faharmony/helpers";
import Tippy, { TippyProps } from "@tippyjs/react/headless";
import { Instance } from "tippy.js";
import { Theme } from "@faharmony/theme";
import { Text } from "../Text";
interface ITooltipProps {
  /** Any element that forwards Ref or a styled component.
   * If the element cannot forward Ref, then wrap the element with
   * `<span tabIndex="0"> <ElementWithoutRef /> </span>` */
  children: TippyProps["children"];
  /** The text shown in the tooltip */
  value: string;
  /** Disable the tooltip @default false */
  disabled?: boolean;
  /** Placement around trigger @default auto */
  placement?: TippyProps["placement"];
}

const StyledTooltip = styled(motion.div)`
  border: 1px solid ${() => Theme.colorOutline()};
  box-shadow: ${() => Theme.shadow("float")};
  border-radius: ${() => Theme.radius("xs")};
  background: ${() => Theme.colorBackground("elevated")};
  padding: ${() => Theme.spacing("sm")} ${() => Theme.spacing()};
`;

/**
 * Tooltip
 * ---
 * Wrap an element with it to add tooltip.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Tooltip value="Tooltip text"> {element} </Tooltip>
 */
const Tooltip = React.forwardRef<HTMLDivElement, ITooltipProps>(
  ({ children, value = "Tooltip", placement = "auto" }, ref) => {
    const initialScale = 0.75;
    const opacity = useSpring(0, Theme.transition());
    const scale = useSpring(initialScale, Theme.transition());

    function onMount() {
      scale.set(1);
      opacity.set(1);
    }

    function onHide({ unmount }: Instance<any>) {
      const cleanup = scale.onChange((value) => {
        if (value <= initialScale) {
          cleanup();
          unmount();
        }
      });

      scale.set(initialScale);
      opacity.set(0);
    }

    const options: TippyProps = useMemo(
      () => ({
        arrow: true,
        render: (attrs) => (
          <StyledTooltip
            ref={ref}
            className="tooltip"
            tabIndex={-1}
            role="tooltip"
            style={{ scale, opacity }}
            {...attrs}
          >
            <Text truncate value={value} variant="small" color="primary" />
          </StyledTooltip>
        ),
        offset: [0, 4],
        placement,
        touch: false,
        onMount,
        onHide,
        zIndex: Theme.zIndex("tooltip"),
      }),
      [value]
    );
    return <Tippy {...options}>{children}</Tippy>;
  }
);

export { Tooltip, ITooltipProps };
