import { useState, useMemo, useLayoutEffect, useEffect } from "react";
import { ICalendarProps } from "./types";

const arrayRange = (start: number, end: number) =>
  Array(end - start + 1)
    .fill(null)
    .map((_, idx) => start + idx);

const weekRange = (start: number, end: number) =>
  start <= end
    ? arrayRange(start, end)
    : end === 1
    ? [...arrayRange(start, 55), 1] // December
    : [...arrayRange(start, 54), ...arrayRange(1, end)]; // January

const getWeekNumber = (date: Date) => {
  date = new Date(date.getTime());
  date.setHours(0, 0, 0, 0);
  date.setDate(date.getDate() + 3 - ((date.getDay() + 6) % 7));
  const week1 = new Date(date.getFullYear(), 0, 4);
  return (
    1 +
    Math.round(
      ((date.getTime() - week1.getTime()) / 86400000 -
        3 +
        ((week1.getDay() + 6) % 7)) /
        7
    )
  );
};

const DAYS = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
const DAYS_OF_THE_WEEK = [
  "Sunday",
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
];
const MONTHS = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];
const DAYS_LEAP = DAYS.map((d) => (d === 28 ? 29 : d));
const MONTHS_SHORT = MONTHS.map((m) => m.slice(0, 3).toLocaleUpperCase());
const DAYS_OF_THE_WEEK_SHORT = DAYS_OF_THE_WEEK.map((d) =>
  d.slice(0, 3).toUpperCase()
);
const getStartDayOfMonth = (date: Date) =>
  new Date(date.getFullYear(), date.getMonth(), 1).getDay();

const isLeapYear = (year: number) =>
  (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;

const isSameDate = (d1: Date, d2: Date) =>
  d1.getDate() === d2.getDate() &&
  d1.getMonth() === d2.getMonth() &&
  d1.getFullYear() === d2.getFullYear()
    ? true
    : false;

// HOOK
const useCalendar = (props: ICalendarProps) => {
  const today = new Date();
  const [selectedDate, setSelectedDate] = useState(
    props.defaultValue || props.value || today
  );
  const [currentView, setCurrentView] = useState(selectedDate);
  const [allWeeksInMonth, setAllWeeks] = useState<{
    [week: number]: { [weekday: number]: Date };
  }>({});
  const { dayNum, currentMonth, currentYear, daysInYear } = useMemo(
    () => ({
      dayNum: currentView.getDate(),
      weekday: currentView.getDay(),
      currentMonth: currentView.getMonth(),
      currentYear: currentView.getFullYear(),
      startDay: getStartDayOfMonth(currentView),
      daysInYear: isLeapYear(currentView.getFullYear()) ? DAYS_LEAP : DAYS,
    }),
    [currentView]
  );

  useLayoutEffect(() => {
    Array(daysInYear[currentMonth])
      .fill(null)
      .forEach((_, i) => {
        const date = new Date(currentYear, currentMonth, i + 1);
        const weekNumber = getWeekNumber(date);
        const obj = { [date.getDay()]: date };
        setAllWeeks((cur) => ({
          ...cur,
          [weekNumber]: Object.assign(cur[weekNumber] || {}, obj),
        }));
      });
  }, [currentView]);

  useEffect(() => {
    setCurrentView(selectedDate);
    props.onChange && props.onChange(selectedDate);
  }, [selectedDate]);
  useEffect(() => {
    props.value && setSelectedDate(props.value);
  }, [props.value]);

  const actions = useMemo(
    () => ({
      handleSelectDate: setSelectedDate,
      handleNextDay: (date: Date = selectedDate) =>
        setSelectedDate(
          new Date(date.getFullYear(), date.getMonth(), date.getDate() + 1)
        ),
      handlePrevDay: (date: Date = selectedDate) =>
        setSelectedDate(
          new Date(date.getFullYear(), date.getMonth(), date.getDate() - 1)
        ),
      handleNextMonth: () =>
        setCurrentView(new Date(currentYear, currentMonth + 1, 1)),
      handlePrevMonth: () =>
        setCurrentView(new Date(currentYear, currentMonth - 1, 1)),
      handleNextYear: () =>
        setCurrentView(new Date(currentYear + 1, currentMonth, 1)),
      handlePrevYear: () =>
        setCurrentView(new Date(currentYear - 1, currentMonth, 1)),
    }),
    [{ dayNum, currentYear, currentMonth, selectedDate }]
  );
  const state = {
    selectedDate,
    currentView,
    allWeeksInMonth,
    currentMonth,
    currentYear,
    daysInYear,
    firstWeekOfMonth: getWeekNumber(new Date(currentYear, currentMonth, 1)),
    lastWeekOfMonth: getWeekNumber(
      new Date(currentYear, currentMonth, daysInYear[currentMonth])
    ),
  };
  return { state, actions };
};

export {
  useCalendar,
  ICalendarProps,
  DAYS_OF_THE_WEEK_SHORT,
  MONTHS_SHORT,
  weekRange,
  isSameDate,
};
