import { styled, Theme, SMLType, CSSProperties } from "@faharmony/theme";
import {
  Globals,
  ContentDistribution,
  ContentPosition,
  SelfPosition,
} from "csstype";

interface IBoxProps {
  /** Width of Box @default '100%' */
  width?: CSSProperties["width"];
  /** Height of Box @default 'auto' */
  height?: CSSProperties["height"];
  /** Spacing between children @default 'md' */
  spacing?: SMLType;
  /** By default, items will all try to fit onto one line.
   * You can change that and allow the items to wrap as needed with this property.
   * @default nowrap */
  wrap?: CSSProperties["flexWrap"];
  /** Overflow @default 'visible' */
  overflow?:
    | "auto"
    | "visible"
    | "scroll"
    | "hidden"
    | "clip"
    | "inherit"
    | "initial"
    | "unset";
  /** Aligns grid items along the inline (row) axis.
   * @default 'auto'
   * [@see MDN](https://developer.mozilla.org/docs/Web/CSS/justify-content) */
  justifyContent?:
    | Globals
    | ContentDistribution
    | ContentPosition
    | "left"
    | "normal"
    | "right"
    | "auto";
  /** This defines the default behavior for how flex items are laid out along the cross axis on the current line.
   * @default center
   * @see https://developer.mozilla.org/docs/Web/CSS/align-items */
  alignItems?: Globals | SelfPosition | "baseline" | "normal" | "stretch";
  /** This aligns a flex container's lines within when there is extra space in the cross-axis,
   * similar to how justify-content aligns individual items within the main-axis. @default baseline */
  alignContent?:
    | Globals
    | ContentDistribution
    | ContentPosition
    | "baseline"
    | "normal";
  /** This establishes the main-axis, thus defining the direction
   * flex items are placed in the flex container. @default row */
  direction?: CSSProperties["flexDirection"];
}

const calcSpacing = (props: IBoxProps) => {
  const { wrap = "nowrap", spacing = "md", direction = "row" } = props;
  const isWrap = wrap === "wrap" || wrap === "wrap-reverse";
  const gap = Theme.spacing(spacing);
  const marginDirection =
    direction === "column"
      ? "top"
      : direction === "column-reverse"
      ? "bottom"
      : direction === "row-reverse"
      ? "right"
      : "left";

  if (isWrap)
    return `
    margin: calc(${gap} * -0.5);
    & > * {
        margin: calc(${gap} * 0.5) !important;
    }
  `;
  else
    return `
  & > * + * {
    margin-${marginDirection}: ${gap} !important;    
  }
  `;
};

/**
 * Box
 * ---
 * Flex Wrapper.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Box>{Other stuff...}</Box>
 */
const Box = styled.div<IBoxProps>`
  position: relative;
  width: ${(props) => props.width || "100%"};
  height: ${(props) => props.height || "auto"};
  display: flex;
  flex-wrap: ${(props) => props.wrap || "nowrap"};
  flex-direction: ${(props) => props.direction || "row"};
  justify-content: ${(props) => props.justifyContent || "auto"};
  align-content: ${(props) => props.alignContent || "initial"};
  align-items: ${(props) => props.alignItems || "center"};
  overflow: ${(props) => props.overflow || "visible"};
  ${calcSpacing};
`;

export { Box, IBoxProps };
