//     window.addEventListener("resize", this.updateStyle);
//   }
//   componentWillUnmount() {
//     window.removeEventListener("resize", this.updateStyle);
//   }
//   render() {
//     const { as: Element = "div", ...props } = this.props;
//     return <Element {...props} style={this.state.style} />;
//   }
