const Div100vh = (props: Div100vhProps) => {
  const [style, setStyle] = React.useState<CSSProperties>({});
  const updateStyle = () =>
    setStyle(convertStyle(props.style, getWindowHeight()));
  React.useLayoutEffect(() => {
    updateStyle();
  }, []);
  useEventListener("resize", updateStyle);
  return <div {...props} style={style} />;
};

export { Div100vh };
